import { Routes,Route} from 'react-router-dom'

import Home from '../pages/Home/Home';
import Contact from '../pages/Contact/Contact';
import Service from '../pages/Service/Service';
const RoutesConfig =(
        <Routes>
            <Route 
                path='/' 
                element={<Home/>}
            />
             <Route 
                path='/contact' 
                element={<Contact/>}
            />
             <Route 
                path='/service' 
                element={<Service/>}
            />
        </Routes>
)
 
export default RoutesConfig;